import { Component, OnInit } from '@angular/core';
import { PasseioService } from '../../services/passeio.service';

@Component({
  selector: 'app-meus-passeios',
  templateUrl: './meus-passeios.page.html',
  styleUrls: ['./meus-passeios.page.scss'],
})
export class MeusPasseiosPage implements OnInit {

    constructor(
      private passeioService: PasseioService
    ) { }

    public passeios: Object = [];

    detalhes(id:String){
      console.log(id)
    }

    ngOnInit() {
      
      try {
        this.passeioService.getMeusPasseios().subscribe(response => {
          this.passeios = response
        })
      } catch (error) {
        console.log(error)
      }

    }

}
